import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Agendar from './views/Agendar.vue';
import Produtos from './views/Produtos.vue';

import Mes from './components/Mes.vue';
import Semana from './components/Semana.vue';
import Periodo from './components/Periodo.vue';
import Dia from './components/Dia.vue';
import Cadastro from './views/Cadastro';
import Login from './views/Login';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login,
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
    {
      path: '/agendar',
      name: 'agendar',
      component: Agendar,
      children: [
        {
          // UserProfile will be rendered inside User's <router-view>
          // when /user/:id/profile is matched
          path: '',
          name: 'agenda.mes',
          component: Mes
        },
        {
          // UserPosts will be rendered inside User's <router-view>
          // when /user/:id/posts is matched
          path: 'semana/:mes',
          name: 'agenda.semana',
          component: Semana
        },
        {
            // UserProfile will be rendered inside User's <router-view>
            // when /user/:id/profile is matched
            path: 'semana/dia/:semana',
            name: 'agenda.dia',
            component: Dia
          },
          {
            // UserPosts will be rendered inside User's <router-view>
            // when /user/:id/posts is matched
            path: 'periodo/',
            name: 'agenda.periodo',
            component: Periodo
          }
      ]
    },
    {
      path: '/produtos',
      name: 'produtos',
      component: Produtos,
    },
    {
      path: '/about',
      name: 'about',
      
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
    },
  ],
});
