<template>
  <v-layout>
    <v-flex xs12 sm6 offset-sm3>
    <span v-show='agendamentos'>
      <v-layout row v-for="agendamento in agendamentos" :key="agendamento.id" class="mt-2">
          <v-flex xs12 sm6 offset-sm3>
            <v-card>
            

                  <v-card-title >
                    <div class="display-1 pl-3 pt-3 black--text">{{agendamento.produto.nome}}</div>
                  </v-card-title>
            
              
              <v-list two-line>
                <v-divider inset></v-divider>
                <v-divider inset></v-divider>
                <v-list-tile >
                  <v-list-tile-action>
                    <v-icon color="indigo">calendar_today</v-icon>
                  </v-list-tile-action>

                  <v-list-tile-content>
                    <v-list-tile-title>{{agendamento.data}} - {{agendamento.periodo}} Período - {{agendamento.turno == 1 ? 'Manhã' : 'Tarde' }}</v-list-tile-title>
                    <v-list-tile-sub-title>Data - Período</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-divider inset></v-divider>
                

                

                <v-divider inset></v-divider>

                <v-list-tile>
                  <v-list-tile-action>
                    <v-icon color="indigo">person</v-icon>
                  </v-list-tile-action>

                  <v-list-tile-content>
                    <v-list-tile-title>{{agendamento.professor.nome}} - Turma: {{agendamento.turma}}</v-list-tile-title>
                    <v-list-tile-sub-title>Professor</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>


                

              </v-list>
                <v-card-actions>
                  <v-btn flat color="orange" @click="excluir(agendamento)">Excluir</v-btn>
                </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
    </span>
      
    <v-card v-show="!agendamentos">

        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">Seus Agendamentos</h3>
            <br>
            <div>Você ainda não realizou nenhum agendamento</div>
          </div>
        </v-card-title>

        <v-card-actions>
          <v-btn flat color="orange" to="/agendar">Agendar</v-btn>
        </v-card-actions>
    </v-card>
  </v-flex>
    
  </v-layout>
</template>

<script>
import {auth} from '../firebase'
  export default {
    data: () => ({
      
    }),
    computed:{
      agendamentos(){
        console.log(this.$store.getters.agendamentos)
        return this.$store.getters.agendamentos
      }
    },
    mounted(){
      auth.onAuthStateChanged((user) =>{
        if (user) { 
          this.$store.dispatch('meusAgendamentos', user)
        } else {
          this.$router.push({
                name: 'login'
            })
        }
        });
      
    },
    methods:{
      excluir(agendamento){
        this.$store.commit('preloader')
        this.$store.dispatch('excluirAgendamento', agendamento).then(()=>{
          this.$store.commit('preloader')
        })
      }
    }

  }
</script>

