<template>
  <v-layout>
    <v-container grid-list-md text-xs-center>
       <p>Período</p>
    <h1>{{date}}</h1>
    <br>
      <p>Manhã</p>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(1, 1)">1</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(2, 1)" >2</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(3, 1)">3</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(4, 1)">4</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(5, 1)">5</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" v-on:click="selecionarPeriodo(6, 1)">
          <v-card-text class="px-0">6</v-card-text>
        </v-card>
      </v-flex>
    <v-spacer/>
    <br>
    <p>Tarde</p>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(1, 2)">1</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(2, 2)">2</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(3, 2)">3</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal" class="mb-2">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(4, 2)">4</v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card dark color="teal">
          <v-card-text class="px-0" v-on:click="selecionarPeriodo(5, 2)">5</v-card-text>
        </v-card>
      </v-flex>
      
  
    </v-container>
    <v-flex>
    <v-dialog
      v-model="dialog"
      width="500"
    >

      <v-card flat>
                <v-card-title primary-title>
                <h4>Informações importantes</h4>
                </v-card-title>
                <v-form>
                <v-select class="mr-3 ml-3"
                  v-model="select"
                  :hint="`${select.nome}`"
                  :items="produtos"
                  item-text="nome"
                  item-value="id"
                  label="Select"
                  persistent-hint
                  return-object
                  single-line
                ></v-select>
                <v-text-field v-model="select.turma" prepend-icon="room" name="Turma" label="Turma" class="mr-2 ml-2" type="number"></v-text-field>
                <v-card-actions>
                <v-btn primary large block class="blue--text" @click="agendar">Agendar</v-btn>
                </v-card-actions>
                </v-form>
            </v-card>
    </v-dialog>
    </v-flex>
  </v-layout>
</template>

<script>
  export default {
    data: () => ({
      dialog: false,
      select: [],
      periodo: 0,
      turno: 1
    }),
    computed:{
      date(){
        if(this.$store.getters.dataSelecionadaFormatada){
          return this.$store.getters.dataSelecionadaFormatada
        }else{
          this.$router.push({name: 'agenda.mes'})
        }
      },
      produtos(){
        return this.$store.getters.produtosSelect
      }
    },
    methods:{
      selecionarPeriodo(periodo, turno){
        this.dialog = true
        this.$store.dispatch('periodoETurnoSelecionados', {periodo: periodo, turno: turno})
        this.$store.dispatch('produtoEmUso')
        this.periodo = periodo
        this.turno = turno
        //this.$store.dispatch('agendamentoSelecionado', {
        //periodo
      //})
      //this.$router.push({name: 'home'})
      
      },
      agendar(){
        this.$store.commit('preloader')
        this.$store.dispatch('agendamentoSelecionado', {
          produtoId: this.select.id,
          turma: this.select.turma,
          periodo: this.periodo,
          turno: this.turno,
          produtoNome: this.select.nome
        }).then(()=>{
          this.$store.commit('preloader')
            this.$snotify.success('Agendamento Realizado com sucesso!')
          this.$router.push({name: 'home'})
        })
          
      }
    }

  }
</script>

<style>
.border{
  border: 1px solid white; 
}
</style>